.page-body__header {
  position: relative;
  margin-bottom: 24px;
}

.header {
  background-color: rgba(0, 0, 0, 0.3);
}
.header--menu-opened {
  background-color: #283645;
}

.header__wrapper {
  width: 280px;
  margin: 0 auto;
  padding: 19.5px 0;
  display: flex;
}

.header--menu-opened .header__wrapper {
  padding: 23px 0;
}

.header__logo {
  margin-right: auto;
}

.header__button {
  position: relative;
  width: 50px;
  height: 24px;
  border: none;
  background-color: transparent;
}
.header__button::before {
  content: "";
  position: absolute;
  top: 11px;
  right: 0;
  width: 50px;
  height: 4px;
  background-color: #ffffff;
  box-shadow: 0 -10px 0 0 #ffffff, 0 10px 0 0 #ffffff;
}

.header__button--active::before {
  top: 10px;
  right: -3px;
  width: 29px;
  height: 3px;
  transform: rotate(45deg);
  box-shadow: none;
}
.header__button--active::after {
  content: "";
  position: absolute;
  top: 10px;
  right: -3px;
  width: 29px;
  height: 3px;
  background-color: white;
  transform: rotate(-45deg);
  box-shadow: none;
}

.nav {
  display: none;
}
.nav--opened {
  display: block;
}

.nav__list {
  position: absolute;
  top: 73px;
  right: 0;
  width: 320px;
  margin: 0;
  padding: 0;
  list-style-type: none;
}

.nav__item {
  background-color: #283645;
  text-align: center;
  box-shadow: 0px -1px 0px 0px #000000, 0px 1px 0px 0px rgba(255, 255, 255, 0.1490196078) inset;
}

.nav__link {
  padding: 15.5px;
  display: block;
  font-family: Open Sans;
  font-size: 18px;
  font-weight: 700;
  line-height: 32px;
  color: #ffffff;
  text-transform: uppercase;
  text-decoration: none;
}

.main-promo::before {
  content: "";
  position: absolute;
  top: 0;
  width: 100%;
